<template>
    <section>
      <div class="head">
        <i class="iconfont" @click="get">&#xe64b;</i>
        <span>我的优惠</span>
      </div>

      <router-link :to="{name:'One'}" class="col-xs-6 col1">红包</router-link>
      <router-link :to="{name:'MyVoucher'}" class="col-xs-6 col1">商家代金卷</router-link>
      <router-view></router-view>
    </section>
</template>

<script>
    export default {
        name: "MyPreferential",
      methods:{
        get(){
          window.history.go(-1)
        }
      }
    }
</script>

<style scoped>
  .iconfont{
    font-family:"iconfont" !important;
    font-size:0.26rem;font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
    float:left;
    color: white;
  }
  .head{
    width: 100%;
    height: 0.5rem;
    background-color: blue;
    line-height:0.5rem ;
    font-size: 0.2rem;
    position: fixed;
    top: 0;
    z-index: 2;
  }
  .head span{
    color: white;
  }
  .col1{
    margin-top: 0.5rem;
    text-align: center;
    height: 0.45rem;
    line-height: 0.45rem;
    background-color: #F5F5F5;
    color: black;
    font-size: 0.2rem;
  }
  .right span{
    margin-left: 0.1rem;
    margin-right: 0.1rem;
  }
</style>



