                <span class="glyphicon glyphicon-menu-right pull-right"></span>
              </li>
            </ul>
          </div>
          <div class="pull-down-right col-xs-6">
            <ul>
              <li v-for="(item, index) in food"  v-if="changeColor == index || JSON.stringify(item).indexOf($store.state.arr1) != -1">
                <p v-for="(i, index1) in item.sub_categories.slice(1)" class="pull-p" :style="{color: changeColor ==ind && changeColor1 == index1?'blue':'#666'}" @click="change1(index1,index,i.id)">
                  <span>{{i.name}}</span>
                  <span>{{i.count}}</span>
                </p>
              </li>
            </ul>
          </div>
        </div>
      </transition>
     <transition name="lxh">
       <div class="pull-down" v-if="showTwo" :class='{classShow:showTwo}'>
         <ul class="ul">
           <li>
             <i class="iconfont i" style="color: #3B87C8">&#xe637;</i>
             <p @click="rank(4)" :style="{color: this.id == 4 ? '#3190E8': '#929292'}">智能排序</p>
             <span class="glyphicon glyphicon-ok" v-if="this.id == 4"></span>
           </li>
           <li>
             <i class="iconfont i" style="color: #5FB4DE">&#xe61b;</i>
             <p @click="rank(5)" :style="{color: this.id == 5 ? '#3190E8': '#929292'}">距离最近</p>
             <span class="glyphicon glyphicon-ok" v-if="this.id == 5"></span>
           </li>
           <li>
             <i class="iconfont i" style="color: #F07373">&#xe6e1;</i>
             <p @click="rank(6)" :style="{color: this.id == 6 ? '#3190E8': '#929292'}">销量最高</p>
             <span class="glyphicon glyphicon-ok" v-if="this.id == 6"></span>
           </li>
           <li>
             <i class="iconfont i" style="color: #EBC445">&#xe644;</i>
             <p @click="rank(1)" :style="{color: this.id == 1 ? '#3190E8' : '#929292'}">起送价最低</p>
             <span class="glyphicon glyphicon-ok" v-if="this.id == 1"></span>
           </li>
           <li>
             <i class="iconfont i" style="color: #43CABB">&#xe639;</i>
             <p @click="rank(2)" :style="{color: this.id == 2 ?'#3190E8': '#929292'}">配送速度最快</p>
             <span class="glyphicon glyphicon-ok" v-if="this.id == 2"></span>
           </li>
           <li>
             <i class="iconfont i" style="color: #ECAA47">&#xe640;</i>
             <p @click="rank(3)" :style="{color: this.id == 3 ?'#3190E8':'#929292'}">评分最高</p>
             <span class="glyphicon glyphicon-ok" v-if="this.id == 3"></span>
           </li>
         </ul>
       </div>
     </transition>
     <transition name="lxh">
