           <i class="iconfont" style="color:#3190E8">{{Courier ? '&#xe631;' : '&#xe766;'}}</i>
           <span :style="{color:Courier? '':'#3190E8'}">蜂鸟专送</span>
         </p>
         <p>商家属性(可以多选)</p>
         <div class="three">
           <p @click="pinpai">
             <i class="iconfont" :style="{color:brand? '#A5E0F3':'#3190E8'}">{{brand ? '&#xe635;' : '&#xe766;'}}</i>
