             <span :style="{color:newShop? '':'#3190E8'}">新店</span>
           </p>
           <p @click="zaixian">
             <i class="iconfont" :style="{color:payment? '#FF4E00':'#3190E8'}">{{payment ? '&#xe62b;' : '&#xe766;'}}</i>
             <span :style="{color:payment? '':'#3190E8'}">在线支付</span>
           </p>
           <p @click="fapiao">
             <i class="iconfont" :style="{color:invoice? '#999999':'#3190E8'}">{{invoice ? '&#xe670;' : '&#xe766;'}}</i>
             <span :style="{color:invoice? '':'#3190E8'}">开发票</span>
           </p>
         </div>
       </div>
       <div class="showThree-div">
         <button class="btn btn-default" @click="empty">清空</button>
         <button class="btn btn-success" @click="ensure"
         >确定
         <span v-if="count !=0">({{count}})</span></button>
       </div>
     </div>
     </transition>
     <Scroll :on-reach-bottom="handleReachBottom" loading-text="null" height="640">
