<script>
  import Vue from 'vue'
    import ShopList from "./ShopList";
  import GoTop from "./goTop";
  import XBT from "./XBT";
    export default {
        name: "FoodClass",
      components: {XBT, GoTop, ShopList},
      data(){
          return {
            cityPoint:{}, //存储搜索城市传来的城市地点
            shopList:[], //存储商店列表
            id: '',//存储排序分类
            http:'https://fuss10.elemecdn.com/',
            food:[],//存储商铺分类
            rankID:[{
              chars:'&#xe637',
              text:'智能排序'
            }],
              // [['&#xe637;', '智能排序'],['&#xe61b;', '距离最近'],['&#xe6e1;', '销量最高'], ['&#xe644;', '起送价最低'],['&#xe639;', '配送速度最快'],['&#xe640;', '评分最高']],//存储排序信息
            showOne:false,
            showTwo:false,
            showThree:false,
            changeColor: -1,
            changeColor1: -1,
            Courier:true,
            brand:true,
            takeOut:true,
            punctuality:true,
            newShop:true,
            payment:true,
            invoice:true,
            count:'',
            ind:'',
            arrAy:false,
            delivery:'',// 配送方式参数
            brandShop: '', // 品牌商家参数
            takeAway:'',// 外卖保参数
            onTime:'', //准时达参数
            newStore:'',// 新店参数
            onLine:'', // 在线支付参数
            invoice1:'',// 开发票参数
            animation:true,
            reqNum:20// 网络请求跳过的条数
          }
      },
      mounted(){
        this.animation = true;
        Vue.axios.get('https://elm.cangdu.org/shopping/restaurants?latitude='+this.cityPoint.latitude+'&longitude='+this.cityPoint.longitude+'&order_by='+this.id, null).then((j, v)=>{
          this.animation = false;
          this.shopList = j.data;
          // console.log(j.data)
        })
      },
      methods: {
          //判断排序点击事件的参数获取不同的数据
        rank(i){
          this.animation = true;
          switch (i) {
            case 1:{
              this.id = 1
              break;
            }
            case 2:{
              this.id = 2
              break;
            }
            case 3:{
              this.id = 3
              break;
            }
            case 4:{
              this.id = 4
              break;
            }
            case 5:{
              this.id = 5
              break;
            }
            case 6:{
              this.id = 6
              break;
            }
          }
          this.showTwo = false;
          Vue.axios.get('https://elm.cangdu.org/shopping/restaurants?latitude='+this.cityPoint.latitude+'&longitude='+this.cityPoint.longitude+'&order_by='+this.id, null).then((j, v)=>{
              this.shopList = j.data;
              this.animation = false;
          })
        },
        change(index){
          this.changeColor = index;
        },
        change1(index,index1,id,){
          this.animation = true;
          this.changeColor1 = index;
          this.ind = index1
          this.showOne = false
          Vue.axios.get('https://elm.cangdu.org/shopping/restaurants?latitude='+this.cityPoint.latitude+'&longitude='+this.cityPoint.longitude+'&restaurant_category_ids[]='+id, null).then((j, v)=>{
            this.shopList = j.data;
            this.animation = false;
            // console.log(j.data)
          })
        },
        empty(){
          this.Courier = true;
          this.brand = true;
          this.takeOut = true;
          this.punctuality = true;
          this.newShop = true;
          this.payment = true;
          this.invoice = true;
          this.count=0;
        },
        fengniao(){
          this.Courier = !this.Courier;
          if (this.Courier == false){
            this.delivery = 1;
            this.count++
          }else {
            this.count--
            this.delivery = '';
          }
        },
        pinpai(){
          this.brand = !this.brand
          if (this.brand == false){
            this.brandShop = 8;
            this.count++
          }else {
            this.count--
            this.brandShop = null
          }
        },
        waimai(){
          this.takeOut = !this.takeOut
          if (this.takeOut == false){
            this.count++;
            this.takeAway = 7
          }else {
            this.count--
            this.takeAway = null
          }
        },
        zhunshi(){
          this.punctuality = !this.punctuality;
          if (this.punctuality == false){
            this.count++
            this.onTime = 9
          }else {
            this.count--
            this.onTime = null
          }
        },
        xindian(){
          this.newShop = !this.newShop
          if (this.newShop == false){
            this.count++
            this.newStore = 5
          }else {
            this.count--
            this.newStore = null
          }
        },
        zaixian(){
          this.payment = !this.payment
          if (this.payment == false){
            this.count++
            this.onLine = 3
          }else {
            this.count--
            this.onLine = null
          }
        },
        fapiao(){
          this.invoice = !this.invoice
          if (this.invoice == false){
            this.count++
            this.invoice1 = 4
          }else {
            this.count--
            this.invoice1 = null
          }
        },
        // 筛选确定按钮
        ensure(){
          this.animation = true;
          // 发起请求 获取筛选数据
          Vue.axios.get(`https://elm.cangdu.org/shopping/restaurants?latitude=${this.cityPoint.latitude}&longitude=${this.cityPoint.longitude}${this.delivery == '' ? '': '&delivery_mode[]='}${this.delivery}&support_ids[]=${this.brandShop}&support_ids[]=${this.takeAway}&support_ids[]=${this.onTime}&support_ids[]=${this.newStore}&support_ids[]=${this.onLine}&support_ids[]=${this.invoice1}`).then(res => {
            this.animation = false;
            this.shopList = res.data;
            // console.log(res.data);
            this.showThree = !this.showThree
            this.showTwo = false
            this.showOne = false
          })
        },

        // 跳转到店铺
        goToStore(data){
          this.$store.commit("store", data);
          this.$router.push({name:'store'});
        },
        // 无限下拉
        handleReachBottom () {
          return new Promise(resolve => {
            this.animation = true;
            setTimeout(() =>{
              Vue.axios.get(`https://elm.cangdu.org/shopping/restaurants?latitude=${this.cityPoint.latitude}&longitude=${this.cityPoint.longitude}${this.delivery == '' ? '': '&delivery_mode[]='}${this.delivery}&support_ids[]=${this.brandShop}&support_ids[]=${this.takeAway}&support_ids[]=${this.onTime}&support_ids[]=${this.newStore}&support_ids[]=${this.onLine}&support_ids[]=${this.invoice1}&offset=${this.reqNum}&order_by=${this.id}`).then((j)=>{
                this.animation = false;
                this.reqNum += 20;
                this.shopList = [...this.shopList, ...j.data];
                // console.log(j.data)
              });
              resolve();
            },1500);
          });
        }
      },
      created() {
        // 获取城市信息
        if (localStorage.getItem("cityObj") !== null){
          this.cityPoint = JSON.parse(localStorage.getItem("cityObj"));
          // console.log(this.cityPoint)
        }
        // 发起网络请求获取商店列表
        Vue.axios.get('https://elm.cangdu.org/shopping/v2/restaurant/category').then(res => {
          const arr = res.data
          arr.splice(0, 1)
          this.food = res.data
            console.log(res.data,this.$store.state.arr1)
        });
      }
    }
</script>

