<template>
    <section class="section">
      <div class="head">
        <i class="iconfont" @click="get">&#xe64b;</i>
        <span>积分商城</span>
      </div>
      <div class="box">
        <p>请重新登录</p>
        <div>请返回后重新进入积分商城~</div>
      </div>
    </section>
</template>

<script>
    export default {
        name: "mall",
      methods:{
          get(){
            window.history.go(-1)
          }
      }
    }
</script>

<style scoped>
.section{
  width: 100%;
  height: 100%;
  text-align: center;
  position: relative;
}
.box{
  position: absolute;
  left: 30%;
  top: 40%;
}
  .section p{
 color: blue;
  }
  .section div{
    color: gray;
  }
.iconfont{
  font-family:"iconfont" !important;
  font-size:0.26rem;font-style:normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
  float:left;
  color: white;
}
  .head{
    width: 100%;
    height: 0.5rem;
    background-color: blue;
    line-height:0.5rem ;
    font-size: 0.2rem;
  }
  .head span{
    color: white;
  }
</style>



