           <router-link :to="{name:'download'}"><button class="btn btn-success">下载或升级客户端</button></router-link>
         </div>

       </div>
    </section>
</template>

<script>
    export default {
        name: "Voucher"
    }
</script>

<style scoped>
  .iconfont{
    font-family:"iconfont" !important;
    font-size:0.8rem;font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
  }
  .content{
    width: 100%;
    height: 100%;
    background-color: lightgray;
  }
  .one{
    text-align: center;
    padding-top: 2.5rem;
  }
</style>



