<template>
    <section>
      <!--头部导航栏-->
      <div class="head">
        <div class="search" @click="goToSearch">
          <span class="glyphicon glyphicon-search" aria-hidden="true"></span>
        </div>
        <span @click="goToCityList" style="width: 1.5rem;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;">{{city.name}}</span>
        <div class="content" @click="goToPer">
          <router-link :to="{name: 'login'}" class="pull-right navRight" v-if="isTrue2"><span class="login">登录|注册</span></router-link>
          <router-link :to="{name: 'mineed'}" class="pull-right navRight" v-if="!isTrue2"><span class="glyphicon glyphicon-user" aria-hidden="true"></span></router-link>
        </div>
      </div>


      <!--轮播图-->
      <div class="swiper">
        <mt-swipe :auto="0" class="wrapPage">
          <!--第一页-->
          <mt-swipe-item class="swiperPage">
              <div v-for="food in foodListOne" @click="goToFoodClass(food.title)">
                <img :src="'https://fuss10.elemecdn.com/' + food.image_url">
                <p>{{food.title}}</p>
            </div>
          </mt-swipe-item>
          <!--第二页-->
          <mt-swipe-item class="swiperPage" >
              <div v-for="food in foodListTwo" @click="goToFoodClass(food.title)">
                <img :src="'https://fuss10.elemecdn.com/' + food.image_url">
                <p>{{food.title}}</p>
              </div>
          </mt-swipe-item>
        </mt-swipe>
      </div>
      <div class="nearby">
        <i class="iconfont">&#xe647;</i>
        <span>附近商家</span>
      </div>
      <!--商家列表页面-->
      <ShopList></ShopList>
      <!--返回顶部-->
      <!--<go-top></go-top>-->
      <XBT v-if="animation"></XBT>
    </section>
</template>

<script>
  import Vue from 'vue'
  import { Swipe, SwipeItem } from 'mint-ui';
  import { Header } from 'mint-ui';
  Vue.component(Header.name, Header);
  Vue.component(Swipe.name, Swipe);
  Vue.component(SwipeItem.name, SwipeItem);
  import { Indicator } from 'mint-ui';
  import ShopList from "./ShopList";
  import GoTop from "./goTop";
  import XBT from "./XBT";
  // 引入loading
    export default {
        name: "takeaway",
      components: {XBT,ShopList},
      data(){
         return{
           foodListOne:[],//存储食品列表前半个数组的值
           foodListTwo:[],//存储食品列表后半个数组的值
           city:{}, //存储传过来的城市数据
           cityPoint:{}, //存储搜索城市传来的城市地点
           localMsg:'',//判断是否为登录状态
           isTrue2:true, // 判断登录状态
           animation:true
         }
      },
      mounted(){
        this.animation = true;
          // 获取城市信息
          this.cityPoint = this.$store.state.city;
          // console.log(this.cityPoint)
        // 发起网络请求根据经纬度获取详细地址
        Vue.axios.get('https://elm.cangdu.org/v2/pois/' + this.cityPoint.latitude+ ','+ this.cityPoint.longitude, null).then((j, v) => {
          this.city = j.data;
          this.animation = false;
          // console.log(this.city)
        });

        // 判断是否登录
        this.localMsg = localStorage.getItem('personObj');
        if (this.localMsg == null){
          this.isTrue2 = true
        } else{
          this.isTrue2 = false
        }
      },
      created(){
          // 发起网络请求,获取食品列表
        Vue.axios.get('https://elm.cangdu.org/v2/index_entry', null).then((j, v)=>{
          this.animation = false;
            // console.log(j.data)
          this. foodListOne = j.data.slice(0, 8);
          this. foodListTwo = j.data.slice(8, 16);
        });
      },
      methods:{
          //搜索标志跳转到搜索食品
        goToSearch(){
          this.$router.push({name:'search'})
        },
        // 跳转到个人中心
        goToPer(){
          this.$router.push({name:'mineed'})
        },
          // 轮播图跳转到食品分类
        goToFoodClass(data){
          this.$store.state.arr1 = data;
          this.$router.push({name:'foodclass'})
        },
        // 跳转到城市列表
        goToCityList(){
          this.$router.push({name:'cityList'});
        }
      }
    }
</script>

<style scoped>
  section{
    width: 100%;
    height: 100%;
    background-color: rgb(245, 245, 245);
  }
 .head{
   display: flex;
   width: 100%;
   height: 0.4rem;
   line-height: 0.4rem;
   background-color: cornflowerblue;
   justify-content: space-between;
   position: fixed;
   z-index: 10000;
 }
  .head>span{
    color: white;
    font-size: 0.16rem;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-align: center;
  }
 .search{
   margin-left: 0.05rem;
 }
  .search>span{
    color: white;
    font-size: 0.2rem;
  }
 .content span{
   color: white;
   margin-right: 0.05rem;
   font-size: 0.2rem;
 }
  .content .login{
   display: inline-block;
   color: white;
   padding-right: 0.1rem;
   font-size: 0.16rem;
 }
  .swiper{
    width: 100%;
    height: 40%;
    padding-top: 0.5rem;
    border-bottom: 0.001rem solid darkgrey;
    background-color: white;
  }
  .wrapPage{
    width: 98%;
    height: 100%;
    margin: 0 auto;
    text-align: center;
  }
  .swiperPage{
    width: 100%;
    height: 100%;
    display: flex;
  }
  .swiperPage>div{
    width: 25%;
    float: left;
    color: gray;
  }
  .swiperPage img{
    width: 80%;
  }
  .nearby{
    width: 100%;
    height: 0.3rem;
    line-height: 0.3rem;
    font-size: 0.13rem;
    color: darkgray;
    /*margin:0 0 0.1rem 0.1rem;*/
  }
  .iconfont{
    font-family:"iconfont" !important;
    font-size:0.15rem;font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
  }
</style>



