<template>
  <section>
    <div class="head">
      <!--左边箭头-->
      <i class="iconfont" @click="get">&#xe64b;</i>
      <span>我的积分</span>
    </div>
    <div class="body">
      <div class="show">
        <div class="top">
          <span class="s1">当前积分</span>
          <router-link :to="{name:'monT'}" class="s2">
            <i class="iconfont2">&#xe63f;</i>
            积分说明
          </router-link>
        </div>
        <br>
        <span class="s3">0</span>
        <span>分</span>
        <br>
        <button class="btn" @click="block">积分兑换商品</button>
      </div>
    </div>
    <div class="foot">
      <span>交 易 明 细</span>
      <i class="iconfont1">&#xe6f2;</i>
      <p>最近30天无积分记录</p >
    </div>
    <div class="dis"  v-if="!show">
       <div class="topT">
         <i class="iconfont7">&#xe611;</i>
         <p>快去下单专区大量积分吧</p>
       </div>
      <button class="btn1" @click="none">确定</button>
    </div>
  </section>
</template>

<script>
  export default {
    name: "MyBalance",
    data(){
      return {
        show:true
      }
    },
    methods:{
      get(){
        window.history.go(-1)
      },
      block(){
      this.show=false
      },
      none(){
        this.show=true
      }
    }
  }
</script>

<style scoped>
  .head{
    width: 100%;
    height: 0.4rem;
    line-height: 0.4rem;
    background-color: #008de1;
    color: white;
    font-size: 0.2rem;
    text-align: center;
    overflow: hidden;
    position: fixed;
    z-index: 2;
  }
  .iconfont{
    font-family:"iconfont" !important;
    font-size:0.2rem;font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
    float: left
  }
  .iconfont7{
    font-family:"iconfont" !important;
    font-size:0.7rem;font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
    color: orange;
  }
  .iconfont2{
    font-family:"iconfont" !important;
    font-size:0.2rem;font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
  }
  .body{
    width: 100%;
    height: 2rem;
    background-color: #008de1;
    padding-top: 10%;
  }
  .show{
    width: 90%;
    height: 96%;
    background-color: white;
    border-radius: 2%;
    margin-left: 5%;
    padding-top: 2%;
    overflow: hidden;
  }
  .s1{
    float: left;
    font-size: 0.14rem;
    padding-left: 2%;
  }
  .s2{
    float: right;
    color: #008de1;
    font-size: 0.12rem;
    padding-right: 2%;
  }
  .s3{
    font-size: 0.5rem;
    margin-left: 2.5%;
  }
  .btn {
    width: 94%;
    height: 0.4rem;
    border-radius: 0.05rem;
    margin-left: 0.1rem;
    color: white;
    font-size: 0.18rem;
    background-color: darkgray;
    margin-top: 0.1rem;
    background-color: orangered;
  }
  .foot{
    width: 100%;
    height: 4.45rem;
    background-color: whitesmoke;
    position: relative;
  }
  .foot>span{
    color: gray;
    position: absolute;
    left: 0.14rem;
    top: 0.17rem;
  }
  .iconfont1{
    font-family:"iconfont" !important;
    font-size:0.4rem;font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
    color: #008de1;
    font-size: 1rem;
    position: absolute;
    left: 1.3rem;
    top: 1rem;
  }
  .foot>p{
    position: absolute;
    left: 1.23rem;
    top: 2.4rem;
    font-size: 0.16rem;
  }
  .dis{
    width: 70%;
    height: 1.4rem;
    background-color: white;
    position: absolute;
    top: 1.6rem;
    left: 0.6rem;
    text-align: center;
    border-top-left-radius: 0.1rem;
    border-top-right-radius: 0.1rem;
  }
  .btn1{
    width: 100%;
    height: 0.4rem;
    color: white;
    font-size: 0.18rem;
    margin-top: 0.1rem;
background-color: green;
    border-bottom-left-radius: 0.1rem;
    border-bottom-right-radius: 0.1rem;
  }
</style>



