<template>
    <section>
      <div class="two">
        <i class="iconfont right" @click="get">&#xe64b;</i>
        <span>下载</span>
      </div>
      <div class="content">
        <i class="iconfonts">&#xe63d;</i><br>
        <p>下载饿了么APP</p>
      </div>
      <button class="btn btn-success">下载</button>
    </section>
</template>

<script>
    export default {
        name: "download",
      methods:{
        get(){
          window.history.go(-1)
        }
      }

    }
</script>

<style scoped>
  .iconfont{
    font-family:"iconfont" !important;
    font-size:0.26rem;font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
    float:left;
  }
  .two{
    width: 100%;
    height: 0.5rem;
    background-color:deepskyblue;
    line-height: 0.5rem;
    text-align: center;
    font-size:0.2rem;
    color: white;

  }
  button{
    width: 60%;
  }
  section{
    text-align: center;
  }
  .iconfonts{
    font-family:"iconfont" !important;
    font-size:2rem;font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
    color: blue;
  }
</style>



