<template>
  <section>
    <Scroll :on-reach-bottom="handleReachBottom" loading-text="null" height="640">
    <div class="shopList">
      <div class="store">
      <div v-for="item in shopList" class="eachStore" @click="goToStore(item)">
        <img :src="'https://elm.cangdu.org/img/' + item.image_path">
        <div class="storeRight">
          <div class="storeRightOne">
            <div>
              <div class="brand">品牌</div>
              <span>{{item.name}}</span>
            </div>

            <div class="icon">
              <span v-for="i in item.supports">{{i.icon_name}}</span>
            </div>
          </div>
          <div class="storeRightTwo">
            <!--评分-->
            <div class="rating">
              <el-rate
                v-model="item.rating"
                disabled
                show-score
                text-color="#ff9900"
                score-template="{value}">
              </el-rate>
            </div>

            <span>月售{{item.recent_order_num}}单</span>
            <div>
              <span class="onlySend" v-if="item.delivery_mode">{{item.delivery_mode.text}}</span>
              <span class="ontime">{{item.supports.length > 1 ? item.supports[1].name : ''}}</span>
            </div>
          </div>
          <div class="storeRightThree">
            <p>¥{{item.float_minimum_order_amount}}起送 / 配送费约¥{{item.float_delivery_fee}}</p>
            <div>
              <span>{{item.distance}} /</span>
              <span>{{item.order_lead_time}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
    </Scroll>
    <go-top></go-top>
    <XBT v-if="animation"></XBT>
  </section>
</template>

<script>
  import Vue from 'vue'
  import XBT from "./XBT";
  import GoTop from "./goTop";
    export default {
        name: "ShopList",
      components: {GoTop, XBT},
      data(){
          return {
            cityPoint:{}, //存储搜索城市传来的城市地点
            shopList:[], //存储商店列表
            animation:true,
            reqNum:20,
            arr:[]
          }
      },
      created(){
        // 获取城市信息
        if (localStorage.getItem("cityObj") !== null){
          this.animation = true;
          this.cityPoint = JSON.parse(localStorage.getItem("cityObj"));
          console.log(this.cityPoint)
        }
        // 发起网络请求获取商店列表
        Vue.axios.get('https://elm.cangdu.org/shopping/restaurants?latitude='+this.cityPoint.latitude+'&longitude='+this.cityPoint.longitude, null).then((j, v)=>{
          this.animation = false;
          this.shopList = j.data;
          // console.log(this.shopList)
        })
      },
      methods:{
        // 跳转到店铺
        goToStore(data){
          this.$store.commit("store", data);
          this.$router.push({name:'store'});
        },
        // 无限下拉
        handleReachBottom () {
          return new Promise(resolve => {
            this.animation = true;
            setTimeout(() =>{
              Vue.axios.get('https://elm.cangdu.org/shopping/restaurants?latitude='+this.cityPoint.latitude+'&longitude='+this.cityPoint.longitude+'&offset='+this.reqNum, null).then((j)=>{
                this.animation = false;
                this.reqNum += 20;
                this.shopList = [...this.shopList, ...j.data];
                console.log(j.data)
              });
              resolve();
            },1500);
            });
        }
      },

    }
</script>

<style scoped>
  section{
    width: 100%;
    height: 100%;
  }
  .shopList{
    width: 100%;
    background-color: white;
  }
  .store{
    width: 100%;
  }
  .eachStore{
    width: 100%;
    height: 1rem;
    border-bottom: 0.01rem solid lightgray;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .eachStore > img{
    width: 20%;
    height: 0.75rem;
  }
  .storeRight{
    width: 79%;
    height: 0.75rem;
    margin-left: 0.05rem;
  }
  .storeRightOne{
    width: 98%;
    display: flex;
    justify-content: space-between;
  }
  .brand{
    display: inline-block;
    background-color: rgb(248, 215, 87);
    font-size: 0.12rem;
    border-radius: 0.03rem;
    line-height: 0.12rem;
    padding: 0.02rem;
  }
  .storeRightOne span{
    font-size: 0.16rem;
    font-weight: bold;
  }
  .icon>span{
    height: 0.2rem;
    font-size: 0.12rem;
    padding: 0.02rem;
    margin: 0 0.01rem;
    background-color: ghostwhite;
    border: 0.01rem solid darkgrey;
    border-radius: 0.03rem;
    color: darkgray;
  }
  .storeRightTwo{
    width: 98%;
    height: 0.3rem;
    font-size: 0.12rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .rating{
    /*width: 1rem;*/
  }
  .onlySend{
    background-color: #298eeb;
    padding: 0.01rem;
    border-radius: 0.03rem;
  }
  .ontime{
    border: 0.01rem solid #298eeb;
    padding: 0.01rem;
    border-radius: 0.03rem;
  }
  .storeRightThree{
    width: 98%;
    display: flex;
    font-size: 0.12rem;
    justify-content: space-between;
  }
  .storeRightThree>div span+span{
    color: #298eeb;
  }
  .iconfont{
    font-family:"iconfont" !important;
    font-size:0.15rem;font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
  }
</style>
<style>
  .el-rate_icon, .el-icon-star-on{
    font-size: 0.02rem;
    width: 0.05rem;
  }
  .mint-swipe-indicator{
    background-color: blue;
    opacity: 1;
  }
</style>



